.toast-root {
  position: fixed;
  padding: 30px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1000; }

.toast-box {
  background-color: rgba(0, 0, 0, 0.65);
  color: #F4F4F4;
  padding: 10px 20px;
  border-radius: 4px;
  text-align: center;
  position: absolute;
  /* center horizontally */
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }
  .toast-box.toast-top {
    top: 10%;
    -webkit-animation: actTop 0.5s;
    animation: actTop 0.5s; }
  .toast-box.toast-middle {
    -webkit-animation: openMiddle 0.5s;
    animation: openMiddle 0.5s; }
  .toast-box.toast-bottom {
    top: 90%;
    -webkit-animation: openBottom 0.5s;
    animation: openBottom 0.5s; }
  .toast-box.toast-errorTopico {
    background-color: white;
    top: 10%;
    -webkit-box-shadow: #ccc 0px 2px 8px 0px;
            box-shadow: #ccc 0px 2px 8px 0px;
    color: #636363;
    -webkit-animation: actTop 0.5s;
    animation: actTop 0.5s; }
    .toast-box.toast-errorTopico:before {
      font-size: 16px;
      margin-right: 4px;
      content: "\e919";
      color: red; }
  .toast-box.toast-yesTopico {
    background-color: white;
    top: 10%;
    -webkit-box-shadow: #ccc 0px 2px 8px 0px;
            box-shadow: #ccc 0px 2px 8px 0px;
    color: #636363;
    -webkit-animation: actTop 0.5s;
    animation: actTop 0.5s; }
    .toast-box.toast-yesTopico:before {
      font-size: 16px;
      margin-right: 4px;
      content: "\e916";
      color: green; }

@-webkit-keyframes actTop {
  from {
    opacity: 0;
    top: -20%; }
  to {
    top: 10%;
    opacity: 1; } }

@keyframes actTop {
  from {
    opacity: 0;
    top: -20%; }
  to {
    top: 10%;
    opacity: 1; } }

@-webkit-keyframes openBottom {
  from {
    opacity: 0;
    top: 120%; }
  to {
    top: 90%;
    opacity: 1; } }

@keyframes openBottom {
  from {
    opacity: 0;
    top: 120%; }
  to {
    top: 90%;
    opacity: 1; } }

@-webkit-keyframes openMiddle {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes openMiddle {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }



