.textarea-base {
  position: relative;
  width: 600px;
  height: 160px;
  overflow: hidden;
  border: #d8d8d8 solid 1px;
  background: #FFF;
  padding-top: 12px;
  padding-bottom: 30px; }
  .textarea-base.textarea-size-large {
    width: 800px;
    height: 160px; }
  .textarea-base.textarea-size-base {
    width: 600px;
    height: 160px; }
  .textarea-base.textarea-size-small {
    width: 500px;
    height: 160px; }
  .textarea-base .textarea-inner {
    border: none;
    width: 100%;
    height: 100%;
    padding: 0 12px;
    line-height: 22px;
    resize: none;
    font-size: inherit; }
    .textarea-base .textarea-inner:focus {
      outline: none; }
  .textarea-base .cha-limit {
    position: absolute;
    right: 5px;
    bottom: 1px;
    line-height: 30px;
    font-size: 12px; }
    .textarea-base .cha-limit .max-cha-num {
      color: #d0d0d0; }



