.page-action {
  text-align: center;
  color: #626262; }
  .page-action .page-num {
    display: inline-block;
    min-width: 30px;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    border: #d8d8d8 solid 1px;
    background: #fff;
    margin: 0 3px;
    cursor: pointer; }
    .page-action .page-num.page-text {
      width: 58px;
      cursor: pointer; }
    .page-action .page-num:hover {
      color: #4b8de5; }
    .page-action .page-num.current, .page-action .page-num.current:hover {
      border-color: #4b8de5;
      background: #4b8de5;
      color: #fff; }
    .page-action .page-num.disabled {
      width: 30px;
      height: 32px;
      border: 0;
      background: none;
      margin: 0; }
    .page-action .page-num.disabled:hover {
      color: #626262; }



